.container {
   width: 100vw;
   height: 100vh;
   background: #090909;
   display: grid;
   place-items: center;
}

.card {
   position: relative;
   padding: 15px 25px;
   max-width: 300px;
   background: #171717;
   border: 1px solid rgba(255,255,255,0.4);
   border-radius: 10px;
   color: white;
   display: flex;
   flex-direction: column;
   align-items: center;
   transition: 0.4s ease;
   box-shadow: 0 6px 16px rgba(0,0,0,0.4);
}

.card:hover {
  border: 1px solid rgba(255,255,255,0.6);
  background: #202020;

}

.generate-btn {
   padding: 14px 30px;
   min-width: 200px;
   font-size: 15px;
   letter-spacing: 0.5px;
   background: #F1F1F1;
   color: black;
   border-radius: 30px;
   border: 0;
   cursor: pointer;
   transition: all 0.4s linear;
}

.generate-btn:hover {
   box-shadow: 0 4px 15px rgba(255,255,255,0.3)
}

.avatar-container {
   position: absolute;
   left: 50%;
   top: -15px;
   transform: translate(-50%,-50%);
   width: 90px;
   height: 90px;
   border-radius: 50%;
   box-shadow: 0 4px 15px rgba(194,9,253,0.4);
   overflow: hidden;
}

.avatar-container .avatar {
   width: 100%;
   height: 100%;
   object-fit: cover;
}

.description-text {
   cursor: pointer;
   letter-spacing: 0.4px;
   line-height: 1.55;
   margin-bottom: 30px;
}

.description-text::selection {
   background: transparent;
}

.text-container {
   position: relative;
}
